import { InjectedCompProps, SummaryPart } from 'topwrite';

interface Props {
    part: SummaryPart;
    canDrop?: (part: SummaryPart) => boolean;
}

export default function Part({ props, Component }: InjectedCompProps<Props>) {
    const { canDrop } = props;

    const canDropItem = (part: SummaryPart) => {
        if (part.getMetadata('reference')) {
            return false;
        }
        if (canDrop) {
            return canDrop(part);
        }
        return true;
    };

    return <Component {...props} canDrop={canDropItem} />;
}
